}

.centerdevices .browseriphone .browser:before {
  content: '\25CF \25CF \25CF';
  letter-spacing: -1px;
  border-radius: 10px;
  color: rgba(0, 0, 0, 0.05);
  background: -webkit-linear-gradient(
    left,
    rgba(255, 99, 91, 1) 0%,
    rgba(255, 99, 91, 1) 45%,
    rgba(255, 99, 91, 1) 45%,
    rgba(255, 195, 48, 1) 45%,
    rgba(255, 195, 48, 1) 70%,
    rgba(41, 207, 66, 1) 70%,
    rgba(41, 207, 66, 1) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 26px;
  line-height: 30px;
  height: 30px;
  padding-left: 8px;
  position: absolute;
  top: 0;
}

.centerdevices .browseriphone .iphone {
  background: linear-gradient(
    to bottom,
    rgba(247, 251, 254, 1) 0%,
    rgba(221, 226, 232, 1) 100%
  );
  box-shadow: inset 0 4px 7px 1px #fff,
    inset 0 -5px 20px rgba(187, 200, 225, 0.35),
    0 2px 6px rgba(40, 39, 66, 0.14), 0 10px 20px rgba(40, 39, 66, 0.06),
    0px 36px 79px -1px rgba(0, 0, 0, 0.15);
  overflow: hidden;
  border-radius: 30px;
  width: 220px;
  height: 440px;
  padding: 50px 8px 60px;
  z-index: 3;
  position: absolute;
  top: 0;
  transform: translate3d(0, 130px, 0);
}

